import PropTypes from 'prop-types';
import { useEffect, useState } from 'react';
// material-ui
import { styled, useTheme } from '@mui/material/styles';
import {
    Avatar,
    Card,
    CardContent,
    Grid,
    LinearProgress,
    List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Typography,
    linearProgressClasses
} from '@mui/material';

// assets
// import TableChartOutlinedIcon from '@mui/icons-material/TableChartOutlined';
import UsbIcon from '@mui/icons-material/Usb';
// import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';

// styles
const BorderLinearProgress = styled(LinearProgress)(({ theme }) => ({
    height: 10,
    borderRadius: 30,
    [`&.${linearProgressClasses.colorPrimary}`]: {
        backgroundColor: '#fff'
    },
    [`& .${linearProgressClasses.bar}`]: {
        borderRadius: 5,
        backgroundColor: theme.palette.primary.main
    }
}));

const CardStyle = styled(Card)(({ theme }) => ({
    background: theme.palette.primary.light,
    marginBottom: '22px',
    overflow: 'hidden',
    position: 'relative',
    '&:after': {
        content: '""',
        position: 'absolute',
        width: '157px',
        height: '157px',
        background: theme.palette.primary[200],
        borderRadius: '50%',
        top: '-105px',
        right: '-96px'
    }
}));

// ==============================|| PROGRESS BAR WITH LABEL ||============================== //

function LinearProgressWithLabel({ path, value, size, ...others }) {
    const theme = useTheme();
    return (
        <Grid container direction="column" spacing={1} sx={{ mt: 1.5 }}>
            <Grid item>
                <Grid container justifyContent="space-between">
                    <Grid item>
                        <Typography variant="h6" sx={{ color: theme.palette.primary[800] }}>
                            {path}
                        </Typography>
                    </Grid>
                    <Grid item>
                        <Typography variant="h6" color="inherit">{`${Math.round(value)}% de ${size}Gb`}</Typography>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item>
                <BorderLinearProgress variant="determinate" value={value} {...others} />
            </Grid>
        </Grid>
    );
}

LinearProgressWithLabel.propTypes = {
    path: PropTypes.string,
    value: PropTypes.number,
    size: PropTypes.number
};

// ==============================|| SIDEBAR MENU Card ||============================== //

const MenuCard = () => {
    const theme = useTheme();
    const [online, setOnline] = useState([]);
    const Update = () => {
        (async () => {
            const url = `http://127.0.0.1:5323/api/status`;
            const response = await fetch(url, {
                headers: { 'Content-Type': 'application/json' }
                // credentials: "include",
            });
            const result = await response.json();
            if (response.status === 200) {
                setOnline(result.online);
            }
        })();
    };
    useEffect(() => {
        Update();
    }, []);
    setTimeout(() => {
        Update();
    }, 5000);
    return (
        <>
            <CardStyle>
                <CardContent sx={{ p: 2 }}>
                    <List sx={{ p: 0, m: 0 }}>
                        <ListItem alignItems="flex-start" disableGutters sx={{ p: 0 }}>
                            <ListItemAvatar sx={{ mt: 0 }}>
                                <Avatar
                                    variant="rounded"
                                    sx={{
                                        ...theme.typography.commonAvatar,
                                        ...theme.typography.largeAvatar,
                                        color: theme.palette.primary.main,
                                        border: 'none',
                                        borderColor: theme.palette.primary.main,
                                        background: '#fff',
                                        marginRight: '12px'
                                    }}
                                >
                                    <UsbIcon fontSize="inherit" />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                sx={{ mt: 0 }}
                                primary={
                                    <Typography variant="subtitle1" sx={{ color: theme.palette.primary[800] }}>
                                        Dispositivos
                                    </Typography>
                                }
                                secondary={<Typography variant="caption">conectados</Typography>}
                            />
                        </ListItem>
                        {online !== null ? (
                            online.map((v) => (
                                <ListItem alignItems="flex-start" disableGutters sx={{ p: 0 }}>
                                    <ListItemText
                                        sx={{ mt: 0 }}
                                        primary={
                                            <LinearProgressWithLabel
                                                path={v.path}
                                                value={(v.used / v.size) * 100}
                                                size={`${Math.round(v.size / 1024)}`}
                                            />
                                        }
                                        secondary={
                                            <Typography variant="caption">
                                                Copiado: {`${Math.round(v.copy)}`}mb = ${`${Math.round((v.copy / 1024) * 5)}`}
                                            </Typography>
                                        }
                                    />
                                </ListItem>
                            ))
                        ) : (
                            <Typography variant="caption">No conectados</Typography>
                        )}
                    </List>
                    {/* <LinearProgressWithLabel value={(v.used / v.size) * 100} size={`${Math.round(v.size / 1024)}`} /> */}
                </CardContent>
            </CardStyle>
        </>
    );
};

export default MenuCard;
